<template>
  <div>
    <div class="col-xs-12 col-sm-6">
      <!-- /.contact-content -->
      <div class="lets-contact">
        <ul class="contact-list recruit-list">
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-envelope"></i>
              {{emailname}}
            </a>
            <p>{{email}}</p>
          </li>
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-th-large"></i>
              {{zhaopin}}
            </a>
            <p>{{zhaopinurl}}</p>
          </li>
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-phone"></i>
              {{zhaopinphone}}
            </a>
            <p>{{zhaopinphonenum}}</p>
          </li>
          <li class="tag1">
            <a
              class="tag"
              v-for="(item,index) in taglist"
              :key="index"
              style="margin:2px;"
            >{{item.tag}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="col-xs-12 col-sm-6">
      <ul class="contact-list recruit-list">
        <li v-for="(item,index) in textlist" :key="index">
          <img :src="item.src" class="good-img">
          <span class="good-text">{{item.content}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      emailname: "",
      email: "tk-hr@thinkoonjt.com",
      zhaopin: "",
      zhaopinurl: "https://www.lagou.com/gongsi/537416.html",
      zhaopinphone: "",
      zhaopinphonenum: "0755-26605585",
      taglist: [
        { tag: "" },
        { tag: "" },
        { tag: "" },
        { tag: "" },
        { tag: "" }
      ],
      textlist: [
        {
          src: "./static/lib/image/contact/good.png",
          content: ";"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content: ";"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            ";"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            "V;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            ";"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content: "!"
        }
      ]
    };
  }
};
</script>




