import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import About from '@/pages/About'
import Authorization from '@/pages/Authorization'
import Case from '@/pages/Case'
// import ChampionCase from '@/pages/ChampionCase'
// import SecooCase from '@/pages/SecooCase'
import Contact from '@/pages/Contact'
import Join from '@/pages/Join'
import Services from '@/pages/Services'


Vue.use(Router)
/*  */
const routes = [{
    path: '/',
    name: '',
    component: Home,
    meta: {
      title: ' | ' // 
    }
  },
  {
    path: '/about',
    name: '',
    component: About,
    meta: {
      title: ' | ' // 
    }
  },
  {
    path: '/authorization',
    name: 'IP',
    component: Authorization,
    meta: {
      title: ' | IP' // 
    }
  },
  {
    path: '/case',
    name: '',
    component: Case,
    meta: {
      title: ' | ' // 
    }
  },
  {
    path: '/contact',
    name: '',
    component: Contact,
    meta: {
      title: ' | ' // 
    }
  },
  {
    path: '/join',
    name: '',
    component: Join,
    meta: {
      title: ' | ' // 
    }
  },
  {
    path: '/services',
    name: '',
    component: Services,
    meta: {
      title: ' | ' // 
    }
  }
  // {
  //   path: '/championCase',
  //   name: '',
  //   component: ChampionCase,
  //   meta: {
  //     title: ' | ' // 
  //   }
  // },
  //   {
  //     path: '/secooCase',
  //     name: '',
  //     component: SecooCase,
  //     meta: {
  //       title: ' | ' // 
  //     }
  //   },
]

const router = new Router({
  routes
});

export default router



