<template>
  <div id="home">
    <div id="all-area" class="all-section">
      <Header></Header>
      <section id="main-slider">
        <div id="main-slide" class="main-slider carousel slide" data-ride="carousel">
          <div class="carousel-inner">
            <div
              v-for="(item,index) in indexslider"
              :key="index"
              @click="linkauthorization(index)"
              class="item"
              :class="{'active':index==clickIndex}"
            >
              <img class="img-responsive" :src="item.src" alt="slider">
              <div class="container">
                <div class="slider-content">
                  <div class="container" style="text-align:center;">
                    <h2 class="animated4">
                      <span>{{item.title}}</span>
                    </h2>
                    <p class="animated6 slide-para sp-descp">
                      <span>{{item.content}}</span>
                    </p>
                    <p class="animated4">
                      <a
                        @click.stop="changelink('about')"
                        class="slider-btn cbiz-btn"
                      >{{item.routername1}}</a>
                      <a
                        @click.stop="changelink('contact')"
                        class="slider-btn cbiz-btn cbiz-btn-border"
                      >{{item.routername2}}</a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <a class="left carousel-control" href="#main-slide" data-slide="prev">
              <span>
                <i class="fa fa-angle-left"></i>
              </span>
            </a>
            <a class="right carousel-control" href="#main-slide" data-slide="next">
              <span>
                <i class="fa fa-angle-right"></i>
              </span>
            </a>
            <div class="click-to-next bottom-top-animation">
              <i></i>
            </div>
          </div>
        </div>
      </section>
      <!--  -->
      <IndexServices></IndexServices>
