                <ol class="carousel-indicators">
                  <li
                    data-target="#goal-slider"
                    :data-slide-to="index"
                    v-for="(item,index) in indexadvantage"
                    :key="index"
                    @click="changeactive(index)"
                    :class="{'active':index==clickIndex}"
                    style="margin:0px 3px;"
                  ></li>
                </ol>
                <div class="carousel-inner">
                  <div
                    class="item"
                    :class="{'active':index==changeIndex}"
                    v-for="(item,index) in indexadvantage"
                    :key="index"
                    @click="changeactive(index)"
