                      <h3>{{item.titleone}}</h3>
                      <p>{{item.contentone}}</p>
                      <br>
                      <h3>{{item.titletwo}}</h3>
                      <p>{{item.contenttwo}}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-6">
              <div class="row">
                <div class="projects">
                  <div class="col-xs-6" v-for="(item,index) in indexadvantageright" :key="index">
                    <div class="pro-item">
                      <a href="javacript:void(0);">
                        <h3 class="achievement">{{item.h3}}</h3>
                      </a>
                      <h2 class="counter">{{item.h2}}</h2>
                      <span class="unit">{{item.span}}</span>
                    </div>
                  </div>
                </div>
              </div>
