                <h3 class="kol-scb">{{item.h3o}}</h3>
                <h3 class="kol-scb">{{item.h3t}}</h3>
                <p class="kol-p">{{item.p1}}</p>
                <p>{{item.p2}}</p>
              </div>
            </div>
            <div
              class="col-xs-12 col-sm-12 col-md-5 col-md-offset-1 col-sm-offset-0 col-xs-offset-0"
            >
              <div class="row">
                <ul class="we-tab">
                  <li
                    data-toggle="tab"
                    :data-target="item.iddataneme"
                    :class="{'active':index==indexmatrix}"
                    v-for="(item,index) in indexmatrixright"
                    :key="index"
                    @click="changeindexmatrixactive(index)"
