                      <div class="we-item baseimglight">
                        <img :src="item.imgsrc">
                        <div class="shady"></div>
                      </div>
                    </div>
                    <div class="col-sm-4 col-md-3" v-if="index==4||index==5||index==6||index==7">
                      <div class="we-item baseimglight">
                        <img :src="item.imgsrc">
                        <div class="shady"></div>
                      </div>
                    </div>
