      this.clickIndex = index;
      const str = 4;
      sessionStorage.setItem("navnum", str);
      this.$router.push("/authorization");
      location.reload();
    },
    changelink(strs) {
      if (strs == "about") {
        const str = 1;
        sessionStorage.setItem("navnum", str);
        this.$router.push("/about");
        location.reload();
      } else if (strs == "contact") {
        const str = 6;
        sessionStorage.setItem("navnum", str);
        this.$router.push("/contact");
        location.reload();
      }
    },
