<template>
  <section class="service">
    <div class="container">
      <div class="row" v-show="show">
        <div class="col-sm-8 col-xs-12 col-sm-offset-2 col-xs-offset-0">
          <div class="section-heading">
            <h2 class="title"></h2>
          </div>
        </div>
      </div>
      <div class="row" v-show="show2">
        <div class="col-sm-8 col-xs-12 col-xs-offset-0">
          <div class="section-heading" style="text-align: left;	margin-left: 6px;">
            <h3></h3>
          </div>
        </div>
      </div>
      <!-- /.row -->
      <div class="row animated bounceIn">
        <div
          v-for="(item,index) in indexserives"
          :key="index"
          class="col-md-6 col-sm-6 service-box service-box-boder-animation"
          data-animation="fadeIn"
          data-animation-delay="01"
        >
          <div class="service-icon">
            <img class="flaticon-img icon-large" :src="item.src" alt="Icon">
          </div>
          <div class="service-content">
            <a href="javacript:void(0);">
              <h4>{{item.title}}</h4>
            </a>
            <p>{{item.content}}</p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  name: "IndexServices",
  data() {
    return {
      show: true,
      show2: false,

      //
      indexserives: [
        {
          src: "./static/lib/image/ls/service1.png",
          title: "",
          content:
            "2"
        },
        {
          src: "./static/lib/image/ls/service2.png",
          title: "",
          content:
            "30"
        },
        {
          src: "./static/lib/image/ls/service3.png",
          title: "",
          content:
            ""
        },
        {
          src: "./static/lib/image/ls/service4.png",
          title: "",
          content:
            ""
        }
      ]
    };
  },
  mounted() {
    this.getItem();
  },
  methods: {
    getItem() {
      if (sessionStorage.getItem("navnum") == 1) {
        this.show = false;
        this.show2 = true;
      } else {
        this.show = true;
        this.show2 = false;
      }
    }
  }
};
</script>


